table.datalist.dataTable{
    tbody {
        tr{
            td {
                vertical-align: middle;

                &:hover{
                    cursor:pointer;
                }

                &.control:before{
                    border:none;
                    height:2em;
                    width:2em;
                    left:calc(50% - .5em);
                    top:calc(50% - .5em);
                    line-height:2em;
                }
            }
        }
    }
}